* {
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(36, 36, 36);
  color: white;
}

.MuiFormLabel-root {
  color: white !important;
}
.MuiButtonBase-root.MuiRadio-root {
  color: white;
}

.MuiFormControl-root,
.MuiInputBase-root,
.MuiInputBase-input {
  width: 100%;
}

.MuiFormControl-root {
  gap: 40px;
}

.MuiFormGroup-root {
  flex-wrap: nowrap;
}

.MuiFormControlLabel-root {
  display: flex;
  align-items: center !important;
  margin: 0 !important;
}

/* HEADER */
#header-container {
  align-items: flex-end;
}

#header-container,
#form-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

#header-container {
  padding: 40px 0;
  background-color: gray;
  background-size: 400% 400%;
  height: 30%;

  /* background: linear-gradient(-45deg, #bfbebd, #837f80, #4f5050, #3d3d3d); */
  /* animation: gradient 15s ease infinite; */
}

#header-content,
#form-content {
  display: flex;
  width: 70%;
  gap: 60px;
  text-align: left;
}

#form-content {
  padding: 25px 0;
  flex-direction: column;
}

#text-fields {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

#books-favicon {
  height: 100%;
}

.columns {
  display: flex;
  align-items: center;
  gap: 20px;
}

input[type="text"],
.MuiSelect-select {
  background-color: rgb(64, 64, 64) !important;
  border-radius: 10px;
}

input[type="text"] {
  color: white;
}

.MuiChip-root {
  background-color: #00cc22 !important;
  font-weight: bold;
}

.MuiInputBase-root.MuiOutlinedInput-root.MuiInputBase-colorPrimary.MuiInputBase-fullWidth.MuiInputBase-formControl.MuiAutocomplete-inputRoot {
  background-color: #404040;
}
